
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.Banner{
    width: 100%;
    height: 90%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    background: #e4f3f4;
}

.conatiner{
    width: 90%;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    background: url(../../../public/images/Vegeterian-Cateogry-Banner.jpg) no-repeat top center;
    background-size: cover;
    padding: 40px 0;
    margin: auto;
}

.right{
    width: 50%;
    margin-left: auto;
    color: white;
    padding: 40px 0;
}

.right h2{
    margin-bottom:15px;
    font-size: 35px;
}

.right h4{
    font-size: 20px;
    background: rgba(242, 237, 237,0.15);
    padding: 5px 60px 5px 10px;
    display: inline-block;
}

.right p{
    display: inline-block;
    margin-bottom: 20px;
    padding: 10px 0 10px 10px;
    font-size: 14px;
    background: #dae5e6;
    color: black;
}

.right .one{
    outline: none;
    border: none;
    padding: 5px 15px;
    border: 2px solid white;
    color: white;
    background: transparent;
    margin-right: 10px;
    font-weight: 600;
    cursor: pointer;
}

.right .two{
    outline: none;
    border: 2px solid white;
    color: white;
    font-weight: 600;
    background: transparent;
    padding: 5px 35px;
    cursor: pointer;

}


.right .one:hover{
    background: white;
    color: black;
    transition: 0.5s ease-in-out;
}

.right .two:hover{
    background: white;
    color: black;
    transition: 0.5s ease-in-out;
}