
.cont{
    width: 100%;
    background:  white;

}

.footer{
    padding-top: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer .box{
    width: 90%;
    /* padding: 0 40px; */
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'League Spartan', sans-serif;
    font-family: 'Roboto', sans-serif;
    font-family: 'Rubik', sans-serif;
}

.footer .box .online{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

}

.footer .box .online h4{
    margin-bottom: 30px;

}

.footer .box .online a{
    text-decoration: none;
    color: rgb(131, 127, 127);
    margin-bottom: 8px;
    font-size: 14px;
}

.customer_policy{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-bottom:48px ;
}

.customer_policy h4{
    margin-bottom: 30px;   
}

.customer_policy a{
    text-decoration: none;
    color: rgb(131, 127, 127);
    margin-bottom: 8px;
    font-size: 14px;
}

.experting{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-bottom:48px ;
    margin-top: 15px;
}

.experting{
    margin-bottom: 35px;    
}

.experting .touch{
    margin:30px 0 0 0;    
}

.experting .touch .icons{
    margin-top: 20px;
}


.experting .touch .icons i{
    color: rgb(131, 127, 127);
    font-size: 20px;
    margin-right: 10px;

}

.experting .img img{
    width: 130px;
    margin-top: 35px;
}   

.experting .img img:nth-child(2){
    width: 115px;
}

.box .return{
    width: 100%;
    display: flex;  
    margin-top: 15px; 
    margin-bottom: auto;
    flex-direction: column;
}


.box .return .first{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}

.box .return .first img{
    width: 50px;
    margin-right: 10px;

}

.box .return .first p b{
    color: black;
}

.box .return .first p{
    font-size: 14px;
    color: rgb(131, 127, 127);

}


/* footer details */

.footer-details{
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    margin-top: 80px ;
    /* padding: 0 40px; */
    font-family: 'League Spartan', sans-serif;
    font-family: 'Roboto', sans-serif;
    font-family: 'Rubik', sans-serif;

}

.footer-details  h4{
    margin-bottom: 20px;
    color: rgb(53, 50, 50);

}

.footer-details p{
    margin-bottom: 80px;
    font-size: 14px;
    color: rgb(131, 127, 127);
}

.footer-details p b{
    margin-bottom: 80px;
    font-size: 14px;
    color: rgb(53, 50, 50);
}

.footer-details .inner{
    margin-left: 60px;
}

.ONLINE_SHOPPING1{
    padding-bottom: 30px;
}

