*{
    margin: 0;
    padding: 0;
}

.navbar{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    background: #e4f3f4;
}

.navbar .btn-group{
    /* width: 100%; */
    margin: 0 0 0 65px;
    padding: 0 40px;
    background: white;
    display: inline-block;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}



.navbar{
    margin-bottom: 20px;

}

.navbar .btn-group button{
    margin: 0 10px;
    padding: 20px 30px;
    outline: none;
    border: none;
    background: transparent;
    border-bottom: 2px solid transparent;
    font-weight: 600;
}

.navbar .btn-group button:hover{
    border-bottom: 2px solid black;

}