*{
    margin: 0;   
    padding: 0;
}

.Box{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

}

.card{
    width: 300px;
    background: white;
    display: inline-block;
    margin: 0 0 10px 10px;
    cursor:pointer;
}

.card .card-contaier{
    padding: 20px;

}

.card .card-contaier .category p{
    padding-bottom: 20px;
    font-size: 14px;
    margin: 0;
}

.card .card-contaier .title{
    padding-bottom: 15px;
}

.card .card-contaier .IMG img{
    width: 100%;
    display: block;
    z-index: 5;
    height: 220px;
}

.card .card-contaier p{
    font-size: 14px;
    margin: 15px 0;
}

.card .card-contaier button{
    outline: 1px solid rgb(175, 172, 172);
    border: none;
    padding: 5px 10px;
    font-size: 14px;
    background: transparent;
    cursor: pointer;
}

.card .card-contaier button:hover{
    background: rgb(222, 222, 222);
    transition: 0.5s;
}